import { useState } from "react";
import { Box, Divider, Stack, Typography } from "@mui/material";

import SymptomSelector from "../components/Symptom/SymptomSelector";
import Differentials from "../components/Diagnosis/Differentials";

export default function Home({ resource }) {
  const allSymptoms = resource.symptoms.read();
  const allDiagnoses = resource.diagnoses.read();
  const sortedSymptomNames = allSymptoms.map(sx => sx.medical_term).sort();
  const [symptoms, setSymptoms] = useState([]);

  function updateFields(obj) {
    setSymptoms(obj.symptoms.map(s => s.medical_term));
  }

  return (
    <Stack sx={{ width: "100%" }}>
      <Differentials symptoms={symptoms} jsonDiagnoses={allDiagnoses} />
      <Divider sx={{ backgroundColor: "navy", padding: "1px", marginY: "20px" }} />
      <Box sx={{ width: "90%", margin: "50px auto" }}>
        <Typography variant="h5" sx={{ fontFamily: "Poppins", color: "#36454f", paddingY: "15px" }}>
          Symptoms
        </Typography>
        <SymptomSelector
          symptoms={symptoms}
          symptomList={sortedSymptomNames}
          jsonSymptoms={allSymptoms}
          updateFields={updateFields}
          label="Symptom Selector"
          id="symptoms"
        />
      </Box>
    </Stack>
  );
}
