import { useState, useEffect } from "react";
import { Box, List, ListItem, Typography } from "@mui/material";

import useCalculator from "../../hooks/useCalculator";

const Differentials = ({ symptoms, jsonDiagnoses }) => {
  const [differentials, setDifferentials] = useState([]);
  const { getDifferentials } = useCalculator();

  useEffect(() => {
    let diagnoses = getDifferentials(jsonDiagnoses, symptoms);
    setDifferentials(diagnoses);
  }, [symptoms]);

  return (
    <Box sx={{ width: "90%", margin: "50px auto" }}>
      <Typography variant="h5" sx={{ fontFamily: "Poppins", color: "#36454f" }}>
        Differential Diagnosis
      </Typography>
      <Typography
        sx={{
          fontWeight: 300,
          color: "#36454f",
          lineHeight: "24px",
          fontSize: "16px",
          ml: "5px"
        }}>
        (Ranked by probability)
      </Typography>
      <List
        sx={{
          listStyleType: "number",
          fontFamily: "Poppins",
          color: "#36454f",
          fontWeight: 300,
          ml: "20px"
        }}>
        {differentials &&
          differentials.map(dx => (
            <ListItem key={dx.diagnosis} sx={{ display: "list-item" }}>
              {dx.diagnosis}
            </ListItem>
          ))}
      </List>
    </Box>
  );
};
export default Differentials;
