import { Autocomplete, TextField, FormControl } from "@mui/material";
import { useEffect, useState } from "react";

const SymptomSelector = ({ symptoms, updateFields, id, label, jsonSymptoms, didSubmit }) => {
  const [didEdit, setDidEdit] = useState(false);
  const symptomList = jsonSymptoms.map(sx => sx.medical_term).sort();
  const [symptomValues, setSymptomValues] = useState([]);

  useEffect(() => {
    if (didSubmit) setSymptomValues([]);
  }, [didSubmit]);

  return (
    <FormControl sx={{ width: "90%" }}>
      <Autocomplete
        disablePortal
        autoHighlight
        size="small"
        id={id}
        getOptionLabel={option => option}
        value={didEdit ? symptomValues : symptoms.map(s => s.medical_term)}
        options={symptomList}
        renderInput={params => (
          <TextField
            {...params}
            label={label}
            sx={{
              fontFamily: "Poppins",
              color: "#36454f",
              marginY: "5px",
              fontSize: "12px"
            }}
            value={didEdit ? symptomValues : symptoms.map(s => s.medical_term)}
          />
        )}
        multiple
        onChange={(e, newValue) => {
          setDidEdit(true);
          setSymptomValues(newValue);
          let temp = [];
          newValue.forEach(sx => {
            let sxObj = jsonSymptoms.filter(s => s.medical_term === sx)[0];
            temp.push(sxObj);
          });
          if (id === "symptoms") {
            updateFields({ symptoms: temp });
          } else if (id === "associated-symptoms") {
            updateFields({ associated_symptoms: temp });
          } else {
            updateFields({ minor: temp });
          }
        }}
        freeSolo
      />
    </FormControl>
  );
};
export default SymptomSelector;

