export default function useCalculator() {
  function getMedicalTerms(symptoms) {
    if (symptoms && symptoms.length > 0) {
      return symptoms.map(s => s.medical_term);
    }
    return [];
  }

  function checkProbability(symptoms, dxObj) {
    let score = 0;

    const dxSymptoms = dxObj.symptoms.map(s => s.medical_term);
    const dxAssociated = getMedicalTerms(dxObj.associated_symptoms);
    const dxMinors = getMedicalTerms(dxObj.minor);

    symptoms.forEach(symptom => {
      if (dxSymptoms.includes(symptom)) {
        score += 2;
      } else if (dxAssociated.includes(symptom)) {
        score += 1;
      } else if (dxMinors.includes(symptom)) {
        score += 0.3;
      }
    });

    return score;
  }

  function getDifferentials(jsonDiagnoses, symptoms) {
    if (symptoms.length === 0) return [];
    let diagnoses = [];

    jsonDiagnoses.forEach(dxObj => {
      let score = checkProbability(symptoms, dxObj);
      if (score >= 3) {
        console.log(dxObj.diagnosis, score);
        diagnoses.push({
          diagnosis: dxObj.diagnosis,
          score
        });
      }
    });

    diagnoses.sort((a, b) => b.score - a.score);
    return diagnoses.slice(0, 5);
  }

  return { checkProbability, getDifferentials };
}
