import axios from "axios";

export default function useFetch() {
  function sortItems(items, term) {
    return items.sort(function (a, b) {
      if (a[term] < b[term]) {
        return -1;
      }
      if (a[term] > b[term]) {
        return 1;
      }
      return 0;
    });
  }

  const wrapPromise = promise => {
    let status = "pending";
    let result = "";
    let suspender = promise.then(
      r => {
        status = "success";
        result = r;
      },
      e => {
        status = "error";
        result = e;
      }
    );

    return {
      read() {
        if (status === "pending") {
          throw suspender;
        } else if (status === "error") {
          throw result;
        }

        return result;
      }
    };
  };

  async function fetchSymptoms() {
    return fetch("/api/symptoms")
      .then(response => response.json())
      .then(data => sortItems(data["symptoms"], "tags"))
      .catch(error => console.log(error));
  }

  const createResource = () => {
    return {
      symptoms: wrapPromise(fetchSymptoms()),
      diagnoses: wrapPromise(fetchDiagnoses())
    };
  };

  async function fetchDiagnoses() {
    return fetch("/api/diagnoses")
      .then(response => response.json())
      .then(data => sortItems(data["diagnoses"], "diagnosis"))
      .catch(error => console.log(error));
  }

  async function fetchDiagnosis(diagnosisId) {
    let dx;
    await axios
      .get(`/api/diagnoses/${diagnosisId}`)
      .then(response => {
        dx = response.data.diagnosis;
      })
      .catch(error => {
        console.log(error);
      });
    return dx;
  }

  async function fetchSymptom(symptomId) {
    let sx;
    await axios
      .get(`/api/symptoms/${symptomId}`)
      .then(response => {
        sx = response.data.symptom;
      })
      .catch(error => {
        console.log(error);
      });
    return sx;
  }

  return { fetchDiagnoses, fetchDiagnosis, fetchSymptom, fetchSymptoms, createResource };
}
